@tailwind base;
@tailwind components;
@tailwind utilities;

.slick-slide>div{
  height: 100px;
  width: 100px;
  margin: 0%;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.bg-header{
  background: rgb(15,42,117);
  background: linear-gradient(333deg, rgba(15,42,117,0.9893207282913166) 7%, rgba(12,46,82,1) 34%, rgba(24,48,73,1) 65%, rgba(3,13,24,1) 93%);
}
header ul li {
  @apply px-2;
  
}
.main-head{
  background-color: rgb(0, 0, 0); 
  justify-content: space-between;
  padding: 10px;
  font-size: larger;
  }
.bg-secondary{
  background:url('/src/Images/bg3.jpg') ;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;

}

.bg-about{
  background: rgb(23,97,178);
  background: linear-gradient(333deg, rgba(23,97,178,1) 0%, rgba(0,0,0,1) 100%);
  
}
.container-image{
  border-radius: 10px;
  background-color: rgba(57, 123, 161, 0.207);
  margin-top: 70px;
  
  height: 70%;
  justify: text-center;
}
.myImage{
  border-radius: 10px;
}

.mobile-nav{
  @apply fixed top-10 left-0 bg-slate-500 w-full h-1/5;
  
}
.mobile-nav li{
  @apply w-full text-center border-b-2 h-full;
  
}
.cv-button{
  background-color: rgba(149, 152, 165, 0.533);
  border-radius:5px ;
  height: 27px;
  width: 97px;
}
.cv-button:hover{
  background-color: rgb(0, 0, 0);
  transform: scale(1.1);
}
.project{
  background-color: black;
}

.chatbot{
  border-radius: 20px;
}
.chatbot:hover {
  transform: scale(1.1);
  
}
.icons:hover {
  transform: scale(1.2);
}
.education{
  background: rgb(0,0,0);
  background: linear-gradient(333deg, rgba(0,0,0,1) 6%, rgba(23,97,178,1) 87%);
  
}
.small-container{
  background-color:rgba(57, 123, 161, 0.207);
  margin: 20px;
  padding: 10px;
  border-radius: 10px;
  justify-content: center;
  
}
.small-container:hover{
  transform: scale(1.1);
  background-color:rgba(57, 123, 161, 0.541);

}
.project-container{
  background-color:rgba(57, 123, 161, 0.207);
  margin: 20px;
  padding: 10px;
  border-radius: 10px;
  justify-content: center;
  
}
.project-container:hover{
  transform: scale(1.1);
  

}
.project-container>div{
  background-color: rgba(255, 255, 255, 0.722);
}

@media (max-width: 100px) {
  .small-container {
    width: 280px;
    
  }
}


.eduHead{
  color: rgb(31, 2, 2);
  font-weight: bold;
  font-size: larger;

  }
  .skillimage {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%; /* Adjust as needed */
  }
  
  .skillimage img {
    max-width: 100%;
    max-height: 100%;
  }
  .mob-skillimage{
    justify-content: center;
    margin: 10px;
    height: 150px;
    width: 150px;
    
  }
  .skillimage :hover{
    transform: scale(1.1);
    
  }
.bg-project{
  background: rgb(23,97,178);
  background: linear-gradient(333deg, rgba(23,97,178,1) 0%, rgba(0,0,0,1) 100%);  
}
.project-container{
  
}
.p-img{
  width: 100px;
  background: rgba(255, 255, 255, 0.74);
}
